import Vue from 'vue';
import Vuex from 'vuex';
import { db, dbFire } from '@/firebase';

Vue.use(Vuex);

dbFire
	.collection('packagePost')
	.get()
	.then(querySnapshot => {
		if (querySnapshot.empty) {
			//this.$router.push('/HelloWorld')
		} else {
			var packagesArray = [];
			querySnapshot.forEach(doc => {
				let packages = doc.data();
				packages.id = doc.id;
				packagesArray.push(packages);
			});
			store.commit('setPackages', packagesArray);
		}
	});

const store = new Vuex.Store({
	state: {
		user: null,
		drawer: false,
		packages: [],
	},
	getters: {
		detailPackages(state) {
			return packageId => {
				return state.packages.find(item => {
					return item.id === packageId;
				});
			};
		},
		user(state) {
			return state.user;
		},
	},
	mutations: {
		setUser(state, payload) {
			state.user = payload;
		},
		setPackages(state, val) {
			if (val) {
				state.packages = val;
			} else {
				state.packages = [];
			}
		},
	},
	actions: {
		login({ commit }, payload) {
			db.auth()
				.signInWithEmailAndPassword(payload.email, payload.password)
				.then(user => {
					const newUser = {
						id: user.uid,
					};
					commit('setUser', newUser);
				});
		},
	},
});

export default store;
