      posts: dbFire.collection("homePost"),
      packagePost: dbFire.collection("packagePost"),
      homePost: dbFire.collection("homePost").doc("HCmnhq4RUzSvm987YuAq")
    };
  },
  methods: {
    bookLink() {
      this.$router.replace("/package");
    },
    aboutLink() {
      this.$router.replace("/about");
    },
    contactLink() {
      this.$router.replace("/contact");
    }
  },
  computed: {
    ...mapState(["packages"]),
    pages() {
      return Math.ceil(this.packages.length / 4);
    },
    paginatedPackages() {
      const start = (this.page - 1) * 4;
      const stop = this.page * 4;

      return this.packages.slice(start, stop);
    }
  },

  watch: {
    page() {
      window.scrollTo(0, 0);
    }
  }
};
