<script>
import { dbFire } from "@/firebase";

export default {
  name: "About",
  data() {
    return {
      aboutus: [],
      page: 1,
      contacts: [
        { icon: "mdi-whatsapp", src: "https://www.whatsapp.com/" },
        { icon: "mdi-instagram", src: "https://instagram.com/" },
        { icon: "Line", src: "https://line.me/id/" },
        { icon: "mdi-facebook-box", src: "https://facebook.com/" },
        { icon: "mdi-twitter-box", src: "https://twitter.com/" }
      ]
    };
  },
  firestore() {
    return {
      aboutus: dbFire.collection("aboutPost").doc("NQ3SRUzg93cm7WDvXnzW"),
      contactus: dbFire.collection("aboutPost").doc("8uGYTpttFhwbfgRHP23B")
    };
  },
  methods: {
    bookLink() {
      this.$router.replace("/package");
    }
  },
  computed: {
    pages() {
      return Math.ceil(this.aboutus.team.length / 3);
    },
    sortAscending() {
      return this.aboutus.team
        .slice()
        .sort((a, b) => (a.role > b.role ? 1 : -1));
    },
    paginatedTeam() {
      const start = (this.page - 1) * 3;
      const stop = this.page * 3;

      return this.sortAscending.slice(start, stop);
    }
  }
};
</script>

